import React, {PureComponent} from 'react'
import {
  Row,
  Col
} from 'antd'
import moment from 'moment'

export default class HeaderComponent extends PureComponent {
  render() {
    const {
      database = {},
      templateConfig = {},
      pageNum,
      totalNum,
      receiveAddr,
      receivePhone,
      requireDeliveryTime,
      pagination
    } = this.props

    return (
      <section className="header">
        {/*  */}
        {templateConfig.headerType === 'H_OUTSTOCK_01' && (
          <Row className="group">
            <Col span={24} className="item">{database.orderNo || ''}</Col>
            <Col span={12} className="item">{database.receiveAddr || ''}</Col>
            <Col span={12} className="item">{database.merchantName || ''}</Col>
            <Col span={12} className="item">{database.orderTime ? moment(database.orderTime).format('YYYY-MM-DD HH:mm:ss') : ''}</Col>
            <Col span={12} className="item">{database.requireDeliveryTime || ''}</Col>
          </Row>
        )}
        {/*  */}
        {templateConfig.headerType === 'H_OUTSTOCK_02' && (
          <Row className="group">
            <Col span={12} className="item">{database.orderNo || ''}</Col>
            <Col span={12} className="item">{database.merchantName || ''}</Col>
            <Col span={12} className="item">{database.receiveAddr || ''}</Col>
            <Col span={12} className="item">{database.sapCusCode || ''}</Col>
            <Col span={12} className="item">{database.orderTime ? moment(database.orderTime).format('YYYY-MM-DD HH:mm:ss') : ''}</Col>
            <Col span={12} className="item">{database.requireDeliveryTime || ''}</Col>
          </Row>
        )}
        {/*  */}
        {templateConfig.headerType === 'H_OUTSTOCK_03' && (
          <Row className="group">
            <Col span={8} className="item"></Col>
            <Col span={8} className="item">{database.orderNo || ''}</Col>
            <Col span={8} className="item"></Col>
            <Col span={8} className="item">{database.receiveAddr || ''}</Col>
            <Col span={8} className="item"></Col>
            <Col span={8} className="item"></Col>
            {/*<Col span={12} className="item">{database.preferDcName || ''}</Col>*/}
            <Col span={12} className="item">{database.receiveUsername || ''}</Col>
            <Col span={12} className="item">{database.orderTime ? moment(database.orderTime).format('YYYY-MM-DD HH:mm:ss') : ''}</Col>
            <Col span={12} className="item">/{database.receivePhone || ''}</Col>
            <Col span={12} className="item">{database.requireDeliveryTime}</Col>
            <Col span={12} className="item">{database.receiveAddr || ''}</Col>
            <Col span={12} className="item"></Col>
          </Row>
        )}
        {/*  */}
        {templateConfig.headerType === 'H_OUTSTOCK_04' &&
          <Row className="group">
            <Col span={8} className="item">{receiveAddr || database.receiveAddr || ''}</Col>
            <Col span={8} className="item"></Col>
            <Col span={8} className="item">{`${pageNum || database.pageNum || 1}/${totalNum || database.totalNum || 1}`}</Col>
            <Col span={8} className="item"></Col>
            <Col span={8} className="item">{receivePhone || database.receivePhone || ''}</Col>
            <Col span={8} className="item" style={{textAlign: 'center'}}>{requireDeliveryTime || database.requireDeliveryTime || ''}</Col>
          </Row>}
        {/*  */}
        {templateConfig.headerType === 'H_OUTSTOCK_05' && (
          <Row className="group">
            <Col span={18}>
              <Row span={12}>
                <Col span={12} className="item">{database.receiveAddr || ''}</Col>
                <Col span={12} className="item"></Col>
              </Row>
              <Row span={12}>
                <Col span={12} className="item">{database.orderTime ? moment(database.orderTime).format('YYYY-MM-DD HH:mm:ss') : ''}</Col>
                <Col span={12} className="item">{database.requireDeliveryTime || ''}</Col>
              </Row>
            </Col>
            <Col span={6} className="item">{database.orderNo || ''}</Col>
          </Row>
        )}
        {/*  */}
        {templateConfig.headerType === 'H_OUTSTOCK_06' && (
          <Row className="group">
            <Col span={24}>{database.orderNo || ''}</Col>
            <Col span={12}>{database.receiveAddr || ''}</Col>
            <Col span={12}>{database.merchantName || ''}</Col>
            <Col span={12}>{database.custCon}</Col>
            <Col span={12}>{database.conTel}</Col>
            <Col span={12} className="item">{database.orderTime ? moment(database.orderTime).format('YYYY-MM-DD HH:mm:ss') : ''}</Col>
            <Col span={12} className="item">{database.requireDeliveryTime || ''}</Col>
          </Row>
        )}
        {templateConfig.headerType === 'H_OUTSTOCK_07' && (
            <Row className="group">
              <Col span={24}>{database.orderNo || ''}</Col>
              <Col span={12}>{database.receiveAddr || ''}</Col>
              <Col span={12}>{database.merchantName || ''}</Col>
              <Col span={12}>{database.contactPerson}</Col>
              <Col span={12}>{database.phone}</Col>
              <Col span={12} className="item">{database.orderTime ? moment(database.orderTime).format('YYYY-MM-DD HH:mm:ss') : ''}</Col>
              <Col span={12} className="item">{database.requireDeliveryTime || ''}</Col>
            </Row>
        )}
      </section>
    )
  }
}



