import React, {PureComponent} from 'react'
import {
  Row,
  Col
} from 'antd'

export default class FooterComponent extends PureComponent {
  render() {
    const {
      database = {},
      templateConfig = {},
      pageNum,
      sum,
      totalSum,
      totalNum,
      sumTraditional,
      totalSumTraditional,
      pagination
    } = this.props
    return (
      <section className="footer">
        {/*  */}
        {templateConfig.footerType === 'F_OUTSTOCK_01' && (
          <Row className="group">
            <Col span={24} className="item">{database.sum || 0}</Col>
            <Col span={24} className="item">{database.merchantName || ''}</Col>
            <Col span={24} className="item">{database.receiveAddr || ''}</Col>
            <Col span={24} className="item">{database.buyerRemarks || ''}</Col>
            <Col span={24} className="signature"></Col>
          </Row>
        )}
        {/*  */}
        {templateConfig.footerType === 'F_OUTSTOCK_02' && (
          <Row className="group">
            <Col span={24} className="item">{database.sum || 0}</Col>
            <Col span={24} className="item">{database.merchantName || ''}</Col>
            <Col span={24} className="item">{database.receiveAddr || ''}</Col>
            <Col span={24} className="item">{database.buyerRemarks || ''}</Col>
            <Col span={8} className="signature"></Col>
            <Col span={8} className="signature"></Col>
            <Col span={8} className="signature"></Col>
          </Row>
        )}
        {/*  */}
        {templateConfig.footerType === 'F_OUTSTOCK_03' && (
          <Row className="group">
            <Col span={24} className="item">{database.sum || 0}</Col>
            <Col span={24} className="item">{database.merchantName || ''}</Col>
            <Col span={24} className="item">{database.receiveAddr || ''}</Col>
            <Col span={24} className="item">{database.buyerRemarks || ''}</Col>
            <Col span={24} className="signature"></Col>
            <Col span={24} className="signature"></Col>
          </Row>
        )}
        {/*  */}
        {templateConfig.footerType === 'F_OUTSTOCK_04' && (
          <Row className="group">
            <Col span={24} className="item">{database.buyerRemarks || ''}</Col>
            <Col span={12} className="signature"></Col>
            <Col span={6} className="signature"></Col>
            <Col span={6} className="signature"></Col>
          </Row>
        )}
        {/*  */}
        {templateConfig.footerType === 'F_OUTSTOCK_05' && (
          <Row className="group">
            <Col span={24} className="item">{database.realTotalPriceWithoutTax || ''}</Col>
            <Col span={24} className="item">{database.taxTotalPrice || ''}</Col>
            <Col span={24} className="item">{database.realTotalPrice || ''}</Col>
            <Col span={24} className="item">{database.merchantName || ''}</Col>
            <Col span={24} className="item">{database.receiveAddr || ''}</Col>
            <Col span={24} className="item">{database.buyerRemarks || ''}</Col>
            <Col span={24} className="signature"></Col>
          </Row>
        )}
        {/*  */}
        {templateConfig.footerType === 'F_OUTSTOCK_06' && (
          <Row className="group">
            <Col span={24} className="item">{database.sum || 0}</Col>
            <Col span={24} className="item">{database.merchantName || ''}</Col>
            <Col span={24} className="item">{database.receiveAddr || ''}</Col>
            <Col span={24} className="item">{database.buyerRemarks || ''}</Col>
            <Col span={8} className="signature"></Col>
            <Col span={8} className="signature"></Col>
            <Col span={8} className="signature"></Col>
          </Row>
        )}
        {/*  */}
        {templateConfig.footerType === 'F_OUTSTOCK_07' &&
          <Row className="group">
            <Col span={12} className="item">{sumTraditional || database.sumTraditional || ''}</Col>
            <Col span={12} className="item">{sum || database.sum || 0}</Col>
            {totalNum === pageNum ? <Col span={12} className="item">{totalSumTraditional || database.totalSumTraditional || ''}</Col> : null}
            {totalNum === pageNum ? <Col span={12} className="item">{totalSum || database.totalSum || 0}</Col> : null}
            <Col span={8} className="item"></Col>
            <Col span={8} className="item"></Col>
            <Col span={8} className="item"></Col>
          </Row>}
        {/*  */}
        {templateConfig.footerType === 'F_OUTSTOCK_08' && (
          <Row className="group">
            <Col span={24} className="item">{database.sum || 0}</Col>
            <Col span={8} className="item"></Col>
            <Col span={8} className="item"></Col>
            <Col span={8} className="item"></Col>
          </Row>
        )}
      </section>
    )
  }
}



